import React from 'react';
import { ComposableMap, Geographies, Geography, Marker } from 'react-simple-maps';

const geoUrl = "https://cdn.jsdelivr.net/npm/world-atlas@2/countries-110m.json";

const markers = [
  { name: "Chicago", coordinates: [-87.6298, 41.8781] },
  { name: "San Francisco", coordinates: [-122.4194, 37.7749] },
  { name: "Richmond", coordinates: [-77.4360, 37.5407] },
  { name: "Washington D.C.", coordinates: [-77.0369, 38.9072] },
  { name: "New York City", coordinates: [-74.006, 40.7128] },
  { name: "Nashville", coordinates: [-86.7816, 36.1627] },
  { name: "Atlanta", coordinates: [-84.3880, 33.7490] },
  { name: "Cleveland", coordinates: [-81.6944, 41.4993] },
  { name: "Toronto", coordinates: [-79.3832, 43.6532] },
  { name: "Ontario", coordinates: [-85.3232, 51.2538] },
  { name: "Irvine", coordinates: [-117.8259, 33.6846] },
];

const TravelMap = () => {
  return (
    <div style={{ maxWidth: '100%', marginTop: '2rem' }}>
      <h2 style={{ textAlign: 'center' }}> Places I've Been</h2>
      <ComposableMap projection="geoEqualEarth">
        <Geographies geography={geoUrl}>
          {({ geographies }) =>
            geographies.map(geo => <Geography key={geo.rsmKey} geography={geo} fill="#DDD" stroke="#FFF" />)
          }
        </Geographies>
        {markers.map(({ name, coordinates }) => (
          <Marker key={name} coordinates={coordinates}>
            <circle r={4} fill="#F53" />
            <text textAnchor="middle" y={-10} style={{ fontFamily: 'system-ui', fontSize: 10 }}>
              {name}
            </text>
          </Marker>
        ))}
      </ComposableMap>
    </div>
  );
};

export default TravelMap;
